.local-content {
  width: 90%;
  max-width: 700px;
  margin: 0 auto;
  // padding-top: 70px;
  padding-top: 70px;
  padding-bottom: 70px;
  background: white;

  width: 880px;
  padding: 20px 15px;
  margin: 15px auto;
  position: relative;
  z-index: 5;
  background: white;

  @media print {
    width: 100%;
    max-width: 1700px;
  }

  // max-height: 880px;
}

@media print {
  /* :host {
    display: none;
  }*/
  :host {
    print-color-adjust: exact;
    -webkit-print-color-adjust: exact;
  }

  .dont-print-hide {
    visibility: hidden !important;
  }

  .local-content {
    padding-top: 40px;
    padding-bottom: 70px;
  }

  body {
    page-break-before: always;
    break-after: always;
    break-inside: auto;
  }

  :host {
    table {
      border: 0px solid !important;

      thead {
        tr {
          th {
            z-index: 9999992929292922929292929292929292929292929292929;
            /* background: red !important;
            background-color: red !important;
            color: red;
            border: red !important;*/
          }
        }
      }

      tbody {
        border-left: 0px !important;
        tr {
          border-left: 0px !important;
          td {
            border-left: 0px;
            border-right: 0px;
          }
        }
      }
    }
  }
}

.mat-footer-row .mat-footer-cell {
  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;
  cursor: pointer;
}

.table-container {
  width: 100%;
  // height: 400px;

  table {
    width: 100%;
  }
}

.billto-rowa {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  padding-top: 27px;

  div {
    h6 {
      font-size: 0.9em;
      font-weight: normal;
    }
  }

  .righty {
    width: 70%;
    h6 {
      &:nth-child(1) {
        font-weight: bold;
      }

      &:nth-child(2) {
        margin-top: 4px;
      }
    }
  }

  .lefty {
    // width: 30%;
    text-align: right;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    column-gap: 5px;

    h6 {
      width: 100%;
    }
  }
}

.mat-header-cell {
  background: rgb(0, 0, 0);
}

.table-container {
  padding-top: 35px;
}

table {
  border: 0px solid !important;

  thead {
    tr {
      th {
        background: var(--app-color-primary); //!important;
        color: #fff;
        border: var(--app-color-primary); // !important;
      }
    }
  }

  tbody {
    border-left: 0px !important;
    tr {
      border-left: 0px !important;
      td {
        border-left: 0px;
        border-right: 0px;
      }
    }
  }
}

.break-name {
  word-wrap: break-word;
  white-space: pre-wrap;
  word-break: normal;
}

.put-text-top {
  vertical-align: top;
}

.to-right {
  margin-left: calc(100% - 270px) !important;
}

.put-text-right {
  text-align: right;
}

.contain-totals {
  padding-left: 10px;
  padding-right: 4px;
  padding-top: 7px;
}

.pad-totals {
  padding-left: 27px;
  padding-right: 7px;
}

.adjust-row-height {
  height: 35px !important;
}

.dont-print {
  @media print {
    display: none !important;
  }
}
